import React from 'react';
import Chimee from 'chimee';
import hls from 'chimee-kernel-hls';
import querystring from "querystring";
import {getDetail} from "../api/movies";

export default class Play extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            chimee: null,
        }
    }

    componentDidMount() {
        let search = this.props.location.search.replace("?", "");
        var params = querystring.parse(search);
        this.setState({
            name: params.name
        });
        getDetail(params).then(data => {
            const chimee = new Chimee({
                wrapper: '#wrapper',
                src: data.data && data.data[0].href,
                controls: true,
                isLive: true,
                kernels: {hls},
                autoplay: true,
            });
            this.setState({chimee, data: data.data})
        })
    }

    render() {
        return (
            <div className="detail">
                <div id={'wrapper'}/>
                <div className="detail-list">{this.state.data.map(item => <span
                    onClick={() => {
                        this.state.chimee.load(item.href)
                    }}
                    className="detail-item">{item.name}</span>)}
                </div>
            </div>
        );
    }
}